.container {
  padding-left: 15px;
  padding-right: 15px;
  width: 100%;
  @media screen and (min-width: 480px) {
    width: 480px;
    margin: 0 auto;
  }
  @media screen and (min-width: 768px) {
    width: 740px;
  }
  @media screen and (min-width: 1280px) {
    width: 1080px;
  }
}
